.p-login {
  font-family: -apple-system, BlinkMacSystemFont, sans-serif;
  background: #131417;
  overflow-x: hidden;
  height: 94vh;
  animation: bg-gradient 18s ease infinite;
  background: linear-gradient(300deg, #392467, #F3CCF3, #A367B1);
  background-size: 180% 180%;

}

@keyframes bg-gradient 
{
  0%{
    background-position:0% 50% ;
  }
  50%{
    background-position:100% 50% ;

  }
  100%{
    background-position:0% 50% ;

  }
  
}

h1 {
  color: #FFD1E3;
  font-size: 35px;
  font-weight: 800;
}

.flex-container {
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}

.content-container {
    padding: 0 40px;
    width: 500px;
    /* height: 350px; */
}


.form-container {
  display: flex;
  justify-content: center;
  align-items: center;
  padding: 20px;
  width: 500px;
  height: 350px;
  border-radius: 12px;
  display: flex;
  justify-content: center;
  flex-direction: column;
  background: #1f1f1f;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.199);
}
.form-container:hover{
    box-shadow: 5px 5px 10px rgba(45, 44, 44, 0.199);
    
}

.subtitle {
  font-size: 20px;
  color: rgb(191, 182, 182);
}

.input-style {
  width: calc(100% - 20px);
  padding: 8px;
  border: 1px solid #555;
  border-radius: 4px;
  font-size: 14px;
  color: #fff;
  background-color: #444;
}

.btn {
  padding:10px 15px;
  margin: 10px 13px;
  font-size: 13px;
  border: none;
  outline: none;
  color: rgb(255, 255, 255);
  background: #111;
  cursor: pointer;
  position: relative;
  z-index: 0;
  border-radius: 10px;
  user-select: none;
  -webkit-user-select: none;
  touch-action: manipulation;
}
.btn:before {
  content: "";
  background: linear-gradient(
    45deg,
    #db3232,
    #c8691b,
    #c6c43c,
    #5bca2f,
    #00ffd5,
    #32449b,
    #9253d4,
    #ff00c8,
    #ff0000
  );
  position: absolute;
  top: -2px;
  left: -2px;
  background-size: 400%;
  z-index: -1;
  filter: blur(5px);
  -webkit-filter: blur(1px);
  width: calc(100% + 4px);
  height: calc(100% + 4px);
  animation: glowing 20s linear infinite;
  transition: opacity 0.3s ease-in-out;
  border-radius: 10px;
}
@keyframes glowing {
  0% {
    background-position: 0 0;
  }
  50% {
    background-position: 400% 0;
  }
  100% {
    background-position: 0 0;
  }
}

.btn:after {
  z-index: -1;
  content: "";
  position: absolute;
  width: 100%;
  height: 100%;
  background: #222;
  left: 0;
  top: 0;
  border-radius: 10px;
}
.work {
  border-radius: 10px;
  overflow: hidden;
  position: relative;
}
.work img {
  object-fit: cover;
  height: 84vh;
  border-radius: 20px;
  display: block;
  width: 100%;
  transform: 0.5s;
}

.layer {
  width: 100%;
  height: 0;
  background: linear-gradient(#FFD1E3, #d2add2);
  border-radius: 20px;
  position: absolute;
  left: 0;
  bottom: 0;
  overflow: hidden;
  text-align: center;
  display: flex;
  justify-content: center;
  flex-direction: column;
  padding: 0 10px;
  font-size: 14px;
  transition:height 1.2s;

}
.layer h3 {
  font-weight: 500;
  color: #131417;
  font-size: 80px;
  margin-top: 60px;
  margin-bottom: 100px;
  font-family:Georgia, 'Times New Roman', Times, serif;

}
.layer p{
  color: #131417;
  font-size: 40px;
  font-family:Georgia, 'Times New Roman', Times, serif;
  padding-bottom: 150px;
}
.layer{
  align-items: center;
}

.work:hover .layer 
{
 height:100%;
}